// footer-7
.footer {
    &__plashka {
        padding: 12px 0px;
        background: var(--color-temno-zelenyy);
        font-weight: 500;
        font-size: 14px;
        line-height: 140%;
        text-align: center;
        color: var(--belyy);
    }

    &__inner {
        padding: 40px 0;
        background: #6e88d3;
    }


    &__btn-up {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 10px;
        font-weight: 500;
        font-size: 14px;
        color: var(--color-temno-zelenyy);

        & div {
            width: 40px;
            height: 40px;
            background: var(--belyy);

            &::after {
                width: 10px;
                height: 10px;
                border-top: 1px solid var(--color-temno-zelenyy);
                border-right: 1px solid var(--color-temno-zelenyy);
                transform: rotate(-45deg);
                margin: 4px 0 0 0;
            }
        }
    }

    &__top {
        &_logo {
            max-width: 100%;
            width: 233px;

        }

        &_row {
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
        }

        &_left {
            display: flex;
            align-items: center;
            gap: 20px;
        }

        &_search-btns {
            padding-right: 20px;
            border-right: 1px solid rgba(85, 100, 115, 0.1);
        }

        &_networks {
            display: flex;
            gap: 6px;

            & a {
                display: block;
                width: 30px;
                height: 30px;
                padding: 7px;
                border-radius: 5px;
                background-color: var(--color-temno-zelenyy);
                border: 1px solid transparent;
                display: flex;
                justify-content: center;
                align-items: center;
                transition: all 0.4s ease;

                & svg path {
                    transition: all 0.4s ease;
                }

                &:hover {
                    background: var(--color-belyy);
                    border: 1px solid var(--color-temno-zelenyy);

                    & svg path {
                        fill: var(--color-temno-zelenyy);
                    }
                }
            }
        }

        &_right {
            text-align: right;
            font-family: var(--font-family);
            font-weight: 400;
            font-size: 14px;
            line-height: 150%;
            text-align: right;
            color: #fff;
        }

        &_right p:first-child {
            margin-bottom: 14px;
            width: 650px;
        }

        &_right p:last-child {
            color: rgba(255, 255, 255, 0.4);
        }

        &_search-btns {
            display: flex;
            align-items: center;
            gap: 20px;
        }

        &_btn-search {
            display: flex;
            align-items: center;
            gap: 10px;
            font-weight: 500;
            border: 1px solid #c7d6dc;
            border-radius: 5px;
            padding: 16px 20px;
            transition: all 0.3s ease;

            &:hover {
                opacity: 0.6;
            }
        }

        &_btn-eye {
            transition: all 0.3s ease;

            &:hover {
                opacity: 0.6;
            }

            &--mobile {
                display: none;
            }
        }

        &_btns {
            display: flex;
            gap: 12px;
        }
    }


    &__info {
        &_row {
            display: flex;
            justify-content: space-between;
            gap: 20px;
        }

        &_item {
            & p:first-child {
                font-size: 14px;
                margin-bottom: 8px;
            }
        }

        &_text {
            display: flex;
            align-items: flex-start;
            gap: 10px;

            & * {
                font-weight: 700;
                font-size: 16px;
                line-height: 140%;
                color: var(--color-temno-zelenyy);
            }

            & span {
                display: block;
                font-weight: 400;
            }

            & a:hover,
            & button:hover {
                text-decoration: underline;
            }
        }

        &_icon {
            display: block;
            width: 30px;
            height: 30px;
            border-radius: 5px;
            background-color: var(--color-svetlo-goluboy-1);
            padding: 5px;
        }

        &_phone {
            & a {
                font-size: 22px;
            }
        }
    }


    &__nav {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        gap: 40px;

        &_item {
            flex-basis: 23%;

            & p {
                font-weight: 700;
                font-size: 18px;
                line-height: 140%;
                color: var(--tekst);
                margin-bottom: 10px;
            }

            & ul {
                display: flex;
                flex-direction: column;
                gap: 10px;

                & a {
                    color: var(--tekst-svetlyy);
                    transition: all 0.3s ease;

                    &:hover {
                        color: var(--color-zelenyy);
                        opacity: 0.7;
                    }
                }
            }
        }

        &_item-menu {
            & ul {
                display: block;
                columns: 2;
                column-gap: 20px;

                & a {
                    display: block;
                    font-weight: 700;
                    font-size: 18px;
                    color: var(--temnyy);
                    margin-bottom: 24px;
                }
            }
        }
    }


    &__bottom {
        display: flex;
        justify-content: space-between;
        gap: 20px;

        &_links {
            display: flex;
            flex-direction: column;
            gap: 10px;

            & a {
                text-decoration: underline;
                color: var(--tekst-svetlyy);

                &:hover {
                    opacity: 0.7;
                }
            }
        }

        &_pays {
            display: flex;
            gap: 10px;
            margin-top: 12px;

            & img {
                display: block;
                width: 80px;
                height: 53px;
                padding: 17px 28px;
                border-radius: 10px;
                background-color: var(--belyy);
            }
        }
    }

    &__copy {
        font-size: 12px;
        line-height: 140%;
        color: rgba(0, 54, 73, 0.6);

        & p:first-child {
            margin-bottom: 10px;
        }
    }
}


// media footer-7
@media (max-width: 1480px) {
    .footer {
        &__top_right {
            margin: 0;
        }
    }
}

@media (max-width: 1185px) {
    .footer {
        &__info {
            &_row {
                flex-wrap: wrap;
                gap: 20px;
            }

            &_item {
                flex: 1 0 45%;
            }
        }

        &__nav_item {
            flex-basis: 45%;
        }
    }
}

@media (max-width: 1000px) {
    .footer__top_right p:first-child {
        width: auto;
    }

    .footer__top_row {
        gap: 110px;
    }
}

@media (max-width: 850px) {
    .footer {
        &__plashka {
            font-size: 12px;
        }

        &__inner {
            padding: 20px 0;
        }



        &__top {
            &_row {
                flex-direction: column;
                gap: 40px;
            }

            &_left {
                width: 100%;
                justify-content: space-between;
            }


            &_right {
                flex-direction: column;
                align-items: stretch;
                width: 100%;
                gap: 30px;
            }

            &_search-btns {
                padding: 0;
                border: none;
            }

            &_btn-search {
                width: 100%;
                justify-content: center;
            }

            &_btns {
                flex: 1 0 100%;
                flex-direction: column;
            }

            &_btn-eye {
                display: none;
            }

            &_btn-eye--mobile {
                display: block;
                position: absolute;
                bottom: 0;
                right: 0;
            }
        }

        &__info {
            &_row {
                flex-direction: column;
                gap: 16px;
            }

            &_item {
                & p:first-child {
                    font-size: 12px;
                }

                &:last-child {
                    order: -1;
                }
            }

            &_text {
                align-items: center;

                &>* {
                    font-size: 14px;
                    font-weight: 500;
                }

                & picture {
                    display: none;
                }

                & button,
                & span {
                    display: inline-block;
                    font-size: 14px;
                    font-weight: 500;
                }
            }

            &_phone a {
                font-size: 18px;
            }
        }

        &__nav {
            gap: 20px;

            &_item {
                flex-basis: 100%;

                & p {
                    font-weight: 500;
                    font-size: 16px;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    margin: 0;
                    transition: all 0.4s ease;

                    &::after {
                        content: "";
                        display: inline-block;
                        width: 8px;
                        height: 8px;
                        border-top: 1px solid var(--tegi);
                        border-right: 1px solid var(--tegi);
                        transform: rotate(135deg);
                        transition: all 0.3s ease;
                    }

                    &.active {
                        margin-bottom: 12px;

                        &::after {
                            transform: rotate(315deg);
                        }
                    }
                }

                & ul {
                    max-height: 0;
                    overflow: hidden;
                    transition: all 0.3s ease;

                    &.active {
                        max-height: 500px;
                    }
                }
            }

            &_item-menu {
                padding-top: 30px;
                border-top: 1px solid rgba(85, 100, 115, 0.1);
                margin-top: 10px;

                & ul {
                    column-gap: 80px;
                    max-height: none;

                    & a {
                        font-weight: 500;
                        font-size: 16px;
                        margin-bottom: 20px;
                    }
                }
            }
        }

        &__bottom {
            flex-direction: column-reverse;
            gap: 20px;

            &_links {
                position: relative;

                & a {
                    font-size: 14px;
                    width: 80%;
                }
            }

            &_pays {
                & img {
                    height: 45px;
                    padding: 10px 28px;
                }
            }
        }
    }

    .footer__top_right p:first-child {
        margin-bottom: 20px;
        padding-left: 0px;
    }

    .footer__top_right {
        font-weight: 400;
        font-size: 14px;
        line-height: 150%;
        text-align: left;
    }

}

