/* service-submenu-1 */
.service-submenu-1 {
    display: none;
    position: fixed;
    top: 144px;
    left: 0;
    right: 0;
    background: #F9F7F6;
    width: 100vw;
    border-radius: 0 0 15px 15px;

    &__row {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        flex-direction: column;
        gap: 40px;
        width: 100%;
        background: #F9F7F6;
        padding: 0px 10px 50px;
       
    }

 

    & .header {
        &__submenu {
            &_tabs {
                width: 100%;
                margin: 0 auto;
            }

            &_tab-btns {
                border-bottom: 1px solid var(--color-svetlo-zelenyy-2);
                border-radius: 10px;
               
                display: flex;
                flex-wrap: wrap;
                width: fit-content;
                margin-bottom: 40px;
            }

            &_tab-btn {
                padding: 20px 50px 20px 50px;
                font-weight: 500;
                font-size: 16px;
                line-height: 140%;
                color: #656675;
                background: transparent;
                transition: all 0.3s ease;
                border-bottom: 1px solid  rgba(243, 238, 233, 0.6);

                &:hover {
                    color: #949497;

                }

                &.active {
                    color: #928f8f;
                    border-bottom: 1px solid #625B5A;
                  
                }
            }

            &_tab-content {
                display: none;
                width: 100%;
                order: 10;

                &.active {
                    display: block;
                }

                & ul {
                    display: grid;
                    grid-template-columns: repeat(4, 1fr);
                    align-items: stretch;
                    gap: 25px 10px;
                    max-height: 174px;
                    overflow-y: auto;

                    &::-webkit-scrollbar {
                        width: 4px;
                    }

                    &::-webkit-scrollbar-thumb {
                        border-radius: 10px;
                        background-color: var(--color-temno-zelenyy);
                    }

                    &::-webkit-scrollbar-track {
                        background-color: var(--izobrazhenie);
                    }

                    & a {
                        font-size: 16px;
                        line-height: 130%;
                        color: var(--tekst);
                        margin-bottom: 6px;
                        // font-family: SangBleu Sunrise;
                        font-weight: 400;





                        & span {
                            padding-top: 6px;
                            display: block;
                            font-weight: 400;
                            font-size: 14px;
                            line-height: 130%;
                            color: #405F6A;

                        }
                    }
                }
            }

            &_stocks {
                border-radius: 20px;
                padding: 20px 30px;
                background: #37384C;
                display: flex;
            }
        }

        &__stock {
            padding: 20px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            gap: 30px;
            flex: 1 0 30%;

            &:not(:last-child) {
                padding-right: 10px;
                border-right: 1px solid #FFFFFF1A;
                margin-right: 10px;
            }

            &_date {
                width: max-content;
                font-weight: 400;
                font-size: 12px;
                color: var(--belyy);
                border-radius: 60px;
                padding: 6px 12px;
                background: #FFFFFF1A;
                margin-bottom: 15px;
            }

            &_title {
                font-weight: 400;
                font-size: 18px;
                // font-family: SangBleu Sunrise;
                line-height: 130%;
                color: var(--belyy);
                margin-bottom: 10px;
            }

            &_subtitle {
                color: rgba(255, 255, 255, 0.8);
            }

            &_link {
                color: var(--belyy);

                &:hover {
                    color: var(--color-svetlo-zelenyy-2);
                }
            }
        }
    }
}



@media (max-width: 1260px) {
    .service-submenu-1 {
        position: static;
        width: 100%;
        margin-top: 10px;
        margin-bottom: 18px;
        transform: translateX(0);

        &__row {
            -webkit-box-orient: vertical;
            -webkit-box-direction: normal;
            -ms-flex-direction: column;
            flex-direction: column;
            gap: 10px;
            width: 100%;
            border: 1px solid var(--zelenyy---100);
            border-radius: 15px;
            padding: 10px;
        }

        & .header {
            &__submenu {
                &_tabs {
                    display: contents;
                }

                &_tab-btns {
                    display: contents;
                }

                &_tab-btn {
                    font-size: 14px;
                    border: 1px solid var(--color-svetlo-zelenyy-2);
                    border-radius: 5px;
                    padding: 12px 36px;
                    width: 100%;
                }

                &__submenu-wrapper {
                    display: contents;
                }

                &_tab-contents {
                    display: contents;
                }

                &_tab-content {
                    width: 100%;
                    padding: 10px 12px;
                    gap: 0;

                    & ul {
                        display: grid;
                        grid-template-columns: repeat(2, 1fr);
                        -webkit-box-pack: justify;
                        -ms-flex-pack: justify;
                        justify-content: space-between;
                        gap: 20px 30px;
                        max-height: none;
                        overflow-y: hidden;
                        padding: 0;

                        & a {
                            font-size: 14px;
                        }
                    }
                }

                &_stocks {
                    flex-basis: 100%;
                    margin-top: 0;
                    order: 100;
                    border-radius: 15px;
                    padding: 20px;
                    gap: 0;
                    overflow-x: auto;
                    scroll-snap-type: x mandatory;

                    &::-webkit-scrollbar {
                        width: 0;
                        height: 0;
                    }
                }
            }

            &__stock {
                position: relative;
                padding: 0 20px;
                margin-right: 0;
                min-width: 300px;
                scroll-snap-align: start;

                &_date {
                    padding: 6px 12px;
                    font-size: 12px;
                }

                &_title {
                    font-size: 18px;
                    margin-bottom: 10px;
                }

                &_subtitle {
                    font-size: 14px;
                    font-weight: 400;
                    max-height: none;
                }
            }

            &__submenu-wrapper {
                display: contents;
            }
        }
    }
}

@media (max-width: 800px) {
    .service-submenu-1 {
        & .header__submenu_tab-content {
            & ul {
                display: grid;
                grid-template-columns: repeat(1, 1fr);
                gap: 18px;

                & a {
                    width: 100%;
                    font-size: 14px;
                }
            }
        }
    }
}

@media (max-width: 540px) {
    .service-submenu-1 .header__stock {
        min-width: 100%;
    }
}