/* header  */

.header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    z-index: 100;
    background-color: #fff;
}

main {
    margin-top: 304px;
    transition: margin-top 0.3s ease;
}

main.shifted {
    margin-top: 256px;
}

.header__logo {
    display: block;
    overflow: hidden;
    // max-width: 136px;
    // max-height: 56px;
    // min-width: 136px;
    // min-height: 48px;
    max-width: 233px;
    width: 100%;
}

.logo__picture {
    width: 100%;
    height: 100%;

    & img {
        width: 100%;
        height: 100%;
    }
}

.header__plashka .header__main,
.header__top {
    &::-webkit-scrollbar {
        width: 0;
        height: 0;
    }
}

.header__plashka {
    background: #6e88d3;
}

.header__plashka_row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
    padding: 12px 0px;
}

.header__plashka.hide,
.header__top.hide {
    padding: 0;
}



/* header__top  */

.header__top {
    padding: 20px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 40px;
    transition: all 0.3s ease;
}

.header__info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 40px;
}

.header__info_item {
    display: flex;
    gap: 10px;
}

.header__info_icon {
    border-radius: 100px;
    width: 24px;
    height: 24px;
    padding: 4px;
    background: #F3EEE9;
    display: flex;
    align-items: center;
    justify-content: center;
}

.header__info_text {
    font-weight: 400;
    font-size: 12px;
    line-height: 140%;
    color: #625B5A;
}

.header__info_text p:first-child {
    font-size: 14px;
    margin-bottom: 3px;
    color: #625B5A;
    font-weight: 400;
}

.header__info_location button {
    font-weight: 700;
    color: #625B5A;
    font-size: 16px;
}

.header__info_text-lic {
    font-weight: 500;
    color: #625B5A;
    font-size: 14px;
}

.header__info_phone a {
    font-weight: 700;
    font-size: 16px;
    color: #625B5A;
    text-wrap: nowrap;
}

.header__info_phone a:hover,
.header__info_location button:hover {
    text-decoration: underline;
}

.header__links {
    display: flex;
    align-items: center;
    gap: 12px;
}

.header__networks {
    display: flex;
    gap: 6px;
}

.header__network {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    border-radius: 100px;
    background: #E2D7D2;
}

.header__network:hover {
    background: #e4dfdd;
}


.header__search {
    position: relative;
    width: 100%;

    & input {
        //background: var(--fony-plashek-bezhevye-svetlo-seryy);
        width: 100%;
        background: #F3EEE999;
        border-radius: 60px;
        opacity: 0.7;
        border: 1px solid #625B5A33;
        color: #625B5A;

        &:hover,
        &:active {

            border: 1px solid #625B5A33;
        }

    }


    & img {
        position: absolute;
        top: 50%;
        right: 20px;
        transform: translate(0, -50%);
    }

    &-wrapper {
        position: relative;
        max-width: 327px;
        width: 100%;
    }

    &_result {
        position: absolute;
        top: calc(100% + 4px);
        right: 0;
        min-width: 200px;
        width: 100%;
        z-index: 20;
        border-radius: 10px;
        padding: 4px;
        background: var(--belyy);
        margin: 0 !important;

        & li a {
            border-radius: 20px;
            padding: 18px 20px 18px 16px !important;
            margin: 0 !important;
            border: none !important;

            &:hover {
                background: var(--color-svetlo-zelenyy-2);
            }
        }

        & .no-results-message {
            padding: 14px 16px;
        }
    }

    &_item {
        &>p {
            font-weight: 600;
            font-size: 14px;
            line-height: 143%;
            color: var(--tekst);
            margin-bottom: 8px;
        }

        &-bottom {
            display: flex;
            justify-content: space-between;
            gap: 15px;
            font-size: 12px;
            line-height: 140%;
            color: var(--tegi);
            opacity: 0.7;

            &>p span {
                color: #000000 !important;
                display: inline !important;
                font-size: 12px !important;
                font-weight: 500;

                &::before {
                    content: "" !important;
                }
            }
        }
    }

}




/* header__main */
.header__main {
    padding: 20px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 30px;
}

.header__menu {
    display: contents;
}

.header__btns {
    display: flex;
    gap: 10px;
}

.header__nav>ul {
    display: flex;
    align-items: center;
    gap: 10px;
    padding-top: 3px;
}

.header__nav>ul>li>a,
.header__nav>ul>li>button {
    border-radius: 30px;
    padding: 10.5px 16px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    background: #fff;
    text-align: center;
    color: #1d1e26;

    &:hover {
        opacity: 0.5;
    }
}

.header__nav>ul>li>button {


    padding-right: 14px;
}

.header__nav_about>button {
    display: flex;
    align-items: center;
    gap: 10px;

    &::after {
        content: "";
        display: block;
        border-radius: 5px;
        width: 6px;
        height: 6px;
        background: #625B5A;
    }
}

.header__nav_service>button {
    padding: 10px;
    background: #F3EEE9;

    border-radius: 60px;
    display: flex;
    align-items: center;
    gap: 10px;
    transition: all 0.3s ease;

}

.header__nav_service-span {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    width: 17px;
    height: 20px;
}

.header__nav_service-span>span {
    border-radius: 5px;
    width: 16px;
    height: 2px;
    background: #37384C;
    transition: all 0.3s ease;
}



/* header__discount */
.header__discount {
    padding: 12px 0;
    background: #E2D7D2;
    font-weight: 500;
    font-size: 14px;
    line-height: 140%;
    text-align: center;
    color: #625B5A;
    transition: all 0.3s ease;
    opacity: 1;
    max-height: 500px;
}

.header__discount.hidden {
    opacity: 0;
    max-height: 0;
    padding: 0;
    overflow: hidden;
}

.header__discount p span:last-child {
    font-size: 14px;
    font-weight: 700;
    line-height: 19.6px;
    text-align: center;
    color: #625B5A;
    text-wrap: nowrap;
}

.stock-future-text {
    font-size: 14px;
    font-weight: 700;
    line-height: 19.6px;
    text-align: center;
    color: #625B5AB2;
    text-wrap: nowrap;
}

.header__discount_text {
    display: flex;
    align-items: center;
    gap: 10px;
    text-align: left;
}

.header__discount_row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
}

.header__discount_more {
    position: relative;
    font-weight: 500;
    font-size: 14px;
    color: #625B5A;
    padding-right: 15px;
    margin-right: 15px;
}

.header__discount_close {
    position: relative;
    font-weight: 400;
    font-size: 14px;
    color: #625B5A;
    opacity: 0.6;
    padding: 0 15px;
    border-left: 1px solid #625B5A;
}

.header-mobile__btns {
    display: none;
}

.header__discount_more:before,
.header__discount_more:after,
.header__discount_close:before,
.header__discount_close:after {
    content: "";
    position: absolute;
    width: 7px;
    height: 1px;
    background: #625B5A;
    top: 48%;
    transform: translate(0, -50%);
    right: 0;
}

.header__discount_more:before {
    transform: rotate(45deg);
    top: calc(50% - 2px);
}

.header__discount_more:after {
    transform: rotate(-45deg);
    top: calc(50% + 2px);
}

.header__discount_close:before {
    width: 10px;
    transform: rotate(45deg);
}

.header__discount_close:after {
    width: 10px;
    transform: rotate(-45deg);
}

.header__discount_more:hover,
.header__discount_close:hover {
    opacity: 0.5;
}



.header__plashka_text {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    color: #dfeeff;

}

.header__plashka_text .header__eye {
    margin-right: 20px;
}

.header__eye svg {
    display: flex;
    align-items: center;
}

.header__links_btns {
    display: flex;
    align-items: center;
}

.header__inner {
    background: #f7f7f7;
}

/*  header__submenu */
.header__submenu {
    display: none;
    z-index: 100;

}

.hide-item {
    position: relative;
}

.hide-item--active {
    .header__submenu {
        display: block;
    }
}

.hide-item--active.header__nav_about>button {
    opacity: 0.5;

    &::after {
        width: 8px;
        height: 8px;
        opacity: 0.5;
    }
}

.hide-item--active.header__nav_service>button {
    background: #625B5A;
    color: var(--belyy);

    .header__nav_service-span span {
        background: var(--belyy);
    }

    .header__nav_service-span span:first-child {
        -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
        transform: rotate(45deg);
        position: relative;
        top: 6px;
    }

    .header__nav_service-span span:nth-child(2) {
        opacity: 0;
    }

    .header__nav_service-span span:last-child {
        -webkit-transform: rotate(-45deg);
        -ms-transform: rotate(-45deg);
        transform: rotate(-45deg);
        position: relative;
        bottom: 7px;
    }
}


/* mobile__wrapper */
.header-mobile__wrapper {
    display: none;

    &-phone {
        order: 0;
    }
}

.header-mobile__burger-btns {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 15px;
}

.header-mobile__eye {
    order: 6;
    text-align: right;
    margin-top: -45px;

    & button {
        transition: all 0.3s ease;

        &:hover {
            opacity: 0.6;
        }
    }
}

.header-mobile__info {
    order: 5;
}

.header-mobile__phone {
    display: flex;
    justify-content: center;
    width: 100%;
    gap: 16px;

    & a {
        font-weight: 700;
        font-size: 18px;
        line-height: 130%;
        color: var(--color-zelenyy);
        padding-right: 16px;
        border-right: 1px solid var(--tegi);
        display: flex;
        align-items: center;
        gap: 6px;
        transition: all 0.3s ease;

        &:hover {
            opacity: 0.6;
        }
    }

    & img {
        display: block;
        width: 20px;
        height: 20px;
        padding: 4px;
        border-radius: 5px;
        background: var(--fon-plashek);
    }

    & p {
        font-size: 12px;
        color: var(--tegi);
    }
}


/* burger__menu */
.burger__menu {
    width: 33px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #6e88d3;
    border-radius: 50%;
}

.ham {
    cursor: pointer;
    -webkit-tap-highlight-color: transparent;
    transition: transform 400ms;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.hamRotate.active {
    transform: rotate(45deg);
}

.hamRotate180.active {
    transform: rotate(180deg);
}

.line {
    fill: none;
    transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
    stroke: white;
    stroke-width: 5.5;
    stroke-linecap: round;
}

.ham1 .top {
    stroke-dasharray: 40 139;
}

.ham1 .bottom {
    stroke-dasharray: 40 180;
}

.ham1.active .top {
    stroke-dashoffset: -98px;
}

.ham1.active .bottom {
    stroke-dashoffset: -138px;
}

.burger__phone-span {
    display: flex;
    font-size: 10px;
    font-weight: 400;
    line-height: 14px;
    color: #616161;
    align-items: center;
}

.burger__phone-span img {
    margin-right: 4px;
}

.burger__phone a {
    font-size: 14px;
    font-weight: 700;
    line-height: 22px;
    color: #625B5A;
}

