        margin-left: -20px;
        justify-content: center;
        display: flex;
    }

    main.shifted {
        margin-top: 191px;
    }

    .header-mobile__wrapper {
        display: block;
    }

    .header__top {
        display: none;
    }



    .header {

        background-color: #FFFFFF;
    }



    .header__inner {
        background: #f7f7f7;
    }

    .header__plashka {
        font-size: 12px;
        background: #6e88d3;

        &_row {
            padding: 10px 0;
            -webkit-box-pack: center;
            -ms-flex-pack: center;
            justify-content: center;


            & p {
                text-align: center;
            }
        }

        &_btns {
            display: none;
        }
    }

    .header__discount {
        position: relative;

        & p {
            font-size: 12px;

            &::before {
                display: none;
            }
        }

        &_row {
            width: 100%;
            -ms-flex-preferred-size: 100%;
            flex-basis: 100%;
            gap: 5px;
        }

        &_more,
        &_close span {
            display: none;
        }

        &_btns {
            -ms-flex-preferred-size: 0;
            flex-basis: 0;
        }

        &_close {
            position: absolute;
            top: 12px;
            right: 8px;
            opacity: 1;

            &::before,
            &::after {
                width: 15px;
            }
        }
    }


    .header__inner {
        display: flex;
        flex-direction: column-reverse;
    }

    .header__info,
    .header__nav,
    // .header__btns,
    .header__networks,
    .header__search-wrapper {
        display: none;
    }

    .header__main {
        padding: 14px 0px;
    }

    .header__top {
        padding: 10px 0 20px;
        border: none;
    }

    .header-mobile__btns {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 20px;
        gap: 20px;
    }

    .header__menu {
        display: none;
    }

    .header__menu--active {
        position: fixed;
        top: 111px;
        right: 0;
        width: 100%;
        z-index: 10;
        height: calc(100% - 138px);
        overflow-y: auto;
        padding: 14px 20px;
        background: #f7f7f7;
        display: flex;
        flex-direction: column;
        gap: 30px;

        &::-webkit-scrollbar {
            width: 3px;
        }

        &::-webkit-scrollbar-track {
            background: #DCDCDD;
            border-radius: 3px;
        }

        &::-webkit-scrollbar-thumb {
            border-radius: 3px;
            background: var(--color-temno-zelenyy);
        }

        & .header__top {
            display: flex;
            flex-direction: column;
            align-items: stretch;
            justify-content: flex-start;
            gap: 20px;
            padding: 20px;
        }

        & .header__nav {
            display: block;
            opacity: 1 !important;
            order: 1;
        }

        & .header__btns {
            display: flex;
            order: 2;

            & button {
                flex: 1 0 auto;
            }
        }

        & .header__info {
            display: flex;
            flex-direction: column;
            align-items: stretch;
            gap: 16px;
            order: 3;
            padding-bottom: 30px;
            border-bottom: 1px solid #625B5A33;
            margin-bottom: 30px;
        }

        & .header__links {
            display: flex;
        }
    }

    .header__plashka_row .header__links {
        display: none;
    }

    .header__menu--active .header__networks {
        display: flex;
    }

    .header__menu--active .header__links {
        margin-bottom: 20px;
    }

    .header__nav {
        display: none;

        &>ul {
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            -webkit-box-orient: vertical;
            -webkit-box-direction: normal;
            -ms-flex-direction: column;
            flex-direction: column;
            -webkit-box-align: stretch;
            -ms-flex-align: stretch;
            align-items: stretch;
            gap: 10px;
            margin-bottom: 0;
            position: relative;

            &>li>a {
                display: block;
                font-weight: 500;
                padding: 8px 0;
            }
        }
    }

    .header__nav_about {
        & button {
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            align-items: center;
            gap: 10px;
            width: 100%;
            font-weight: 500;
        }
    }

    .header__nav_service button {
        width: 100%;
        justify-content: center;
    }

    .header__nav_list {
        position: relative;
        z-index: 10;

    }
}

