.popup-change {
    &__content {
        max-width: 948px;
        border-radius: 20px;
        padding: 60px;
        box-shadow: 0 0 30px 0 rgba(255, 255, 255, 0.1);
        background: var(--bg-svetlo-seryy);
        display: flex;
        align-items: center;
    }

    &__left {
        padding-right: 40px;
        border-right: 1px solid var(--color-svetlo-zelenyy-2);
        margin-right: 40px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 20px;
        flex-basis: 60%;
    }

    &__title {
        font-weight: 700;
        font-size: 30px;
        line-height: 120%;
        color: var(--tekst);
        margin-bottom: 12px;
    }

    &__subtitle {
        font-size: 16px;
        line-height: 150%;
        color: var(--tekst-svetlyy);
    }

    &__phone p {
        font-size: 14px;
        color: var(--tekst-svetlyy);
        margin-bottom: 6px;
    }

    &__form {
        display: flex;
        flex-direction: column;
        gap: 12px;
    }

    &__input:hover {
        background-color: var(--belyy);
    }

    &__close {
        width: 40px;
        height: 40px;
        top: 0;
        right: -50px;
    }
}




.popup-change-response {
    &__content {
        max-width: 606px;
        padding: 10px;
        display: flex;
        flex-direction: column;
    }

    &__wrapper {
        & h3 {
            font-family: var(--font-family);
            font-weight: 600;
            font-size: 30px;
            line-height: 140%;
            text-align: center;
            color: #1d1e26;
            margin-bottom: 4px;
        }

        & p {
            font-family: var(--font-family);
            font-weight: 400;
            font-size: 16px;
            line-height: 140%;
            text-align: center;
            color: #667085;
            margin-bottom: 30px;

        }
    }

    &__btn {
        font-family: var(--font-family);
        font-weight: 600;
        font-size: 16px;
        line-height: 140%;
        color: #fff;
        background: #6e88d3;
        border-radius: 30px;
        /* padding: 16px 40px; */
        width: 144px;
        height: 44px;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 20px;
        cursor: pointer;


    }
}

.popup-change__content {
    display: flex;
    align-items: center;
    background: #fff;
    border-radius: 20px;
    padding: 40px;
    width: 541px;
    height: 348px;
}

.popup-change__content-picture {
    width: 96px;
    height: 96px;
}

// media popup-change-1
@media (max-width: 1050px) {
    .popup-change {
        &__content {
            border-radius: 10px;
            padding: 40px 20px;
            flex-direction: column;
            max-width: 480px;
        }

        &__left {
            padding-right: 0;
            border-right: none;
            margin-right: 0;
            gap: 20px;
            flex-basis: 100%;
            align-items: flex-start;
            padding-bottom: 24px;
            border-bottom: 1px solid rgba(190, 219, 215, 0.4);
            margin-bottom: 24px;
        }

        &__title {
            font-size: 18px;
        }

        &__subtitle {
            font-size: 14px;
        }

        &__phone {
            & a {
                font-size: 20px;
            }

            & svg {
                width: 20px;
                height: 20px;
            }
        }
    }

    .popup-change-response {
        &__content {
            max-width: 400px;
            padding: 10px;
            display: flex;
            flex-direction: column;
        }

        &__wrapper {
            & h3 {
                border-radius: 5px;
                padding: 16px 10px;
                font-size: 30px;
            }

            & p {
                gap: 14px;
                padding: 0 10px 20px;
            }
        }

        &__btn {
            padding-top: 20px;
            margin: 0 20px 20px;
        }
    }
}

@media (max-width: 800px) {
    .popup-change__content-picture {
        width: 60px;
        height: 60px;
    }

    .popup-change-response__wrapper p {
        display: none;
    }

    .popup-change-response__wrapper h3 {
        font-family: var(--font-family);
        font-weight: 600;
        font-size: 22px;
        line-height: 140%;
        text-align: center;
        color: #1d1e26;
    }

    .popup-change-response__content {
        max-height: max-content;
        border-radius: 20px;
        padding: 30px;
    }

    .popup-change-response__btn {
        margin: 0px;
        width: 100%;
    }
}

@media (max-width: 600px) {
    .popup-change {
        &__close {
            width: 30px;
            height: 30px;
            top: -40px;
            right: 0;
        }
    }
}