@media (max-width: 1100px) {
    .article {
        &__grid {
            display: grid;
            grid-template-columns: 1fr;
            gap: 0;
        }

        &__right {
            display: contents;
        }

        &__left {
            display: contents;
        }

        & h2,
        & h3,
        & h4,
