// page-articles-1
.page-articles {
    &__search {
        position: relative;
        width: 100%;
        display: flex;
        justify-content: center;
        gap: 10px;


        & svg {
            cursor: pointer;
        }

        & input {
            font-family: var(--font-family);
            font-weight: 400;
            font-size: 16px;
            line-height: 140%;
            color: rgba(60, 60, 67, 0.6);
            background: #fff !important;
            border-radius: 30px;
            padding: 16px 20px;
            width: 690px;
            height: 50px;

            // &:focus {
            //     opacity: 1;
            // }
        }

        & input:hover {
            border: 1px solid #6e88d3;
            background-color: white !important;
        }


        & img {
            position: absolute;
            top: 50%;
            right: 20px;
            transform: translate(0, -50%);
        }
    }



    &__sort {
        &_text {
            color: var(--tekst);
            margin-bottom: 20px;
        }

        &_item {
            margin-bottom: 20px;
            display: flex;
            gap: 14px;
            align-items: center;

            & input[type="checkbox"] {
                position: relative;
                width: 28px;
                height: 28px;
                color: #000;
                border: 1px solid #788189;
                border-radius: 5px;
                appearance: none;
                outline: 0;
                cursor: pointer;
                transition: all 175ms cubic-bezier(0.1, 0.1, 0.25, 1);

                &::before {
                    position: absolute;
                    content: '';
                    display: block;
                    top: 3px;
                    left: 9px;
                    width: 8px;
                    height: 14px;
                    border-style: solid;
                    border-color: #fff;
                    border-width: 0 3px 3px 0;
                    transform: rotate(45deg);
                    opacity: 0;
                }

                &:checked {
                    color: #fff;
                    border-color: var(--color-zelenyy);
                    background: var(--color-zelenyy);

                    &::before {
                        opacity: 1;
                    }
                }
            }

            & label {
                position: relative;
                font-weight: 500;
                font-size: 18px;
                line-height: 140%;
                color: var(--tekst);

                &::before {
                    position: absolute;
                    content: attr(data-content);
                }
            }
        }

        &_quantity {
            font-size: 14px;
            color: var(--tekst-svetlyy);
        }
    }

    &__card {
        display: flex;
        gap: 10px;
        margin-bottom: 40px;

        &_info {
            flex: 1 0 60%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            padding: 30px;
            border: 1px solid #eee;
            border-radius: 20px;
        }

        &_img {
            flex: 1 0 30%;
            display: block;
            min-height: 282px;
            max-width: 33%;
            width: 100%;

            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                border-radius: 20px;
            }
        }

        &_title {

            margin-bottom: 16px;
            font-family: var(--font-family);
            font-weight: 500;
            font-size: 26px;
            line-height: 140%;
            color: #101828;

        }

        &_labels {
            display: flex;
            gap: 10px;
            flex-wrap: wrap;
            margin-bottom: 12px;
        }


        &_description {
            margin-bottom: 32px;
            font-family: var(--font-family);
            font-weight: 400;
            font-size: 16px;
            line-height: 150%;
            color: #474747;
        }

        &_bottom {
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 20px;
            font-size: 14px;
            line-height: 140%;
            color: rgba(64, 95, 106, 0.7);
            text-align: right;

            & span {
                text-wrap: nowrap;
            }
        }

        &_btn {
            border-radius: 10px;
            padding: 10px 20px;
            background: #e3edee;
        }
    }
}

