.page-articles__popular-item {
    width: 212px;
    min-width: 212px;
    transition: width 0.3s ease;
    border-radius: 20px;
    height: 260px;
    backdrop-filter: blur(49.20000076293945px);
    background: #d9ebff;
    cursor: pointer;
}

.page-articles__popular-item img {
    border-radius: 20px;
    opacity: 0;
    transform: scale(0);
    object-fit: cover;
    width: 100%;
    height: 100%;
    /* Начальный масштаб */
    transition: opacity 0.6s ease, transform 0.6s ease;
    /* Плавное изменение прозрачности и масштаба */
}



/* Первый элемент по умолчанию шириной 433px */
.page-articles__popular-item:first-child {
    width: 433px;
}

.page-articles__popular-circle {
    position: absolute;
    top: auto;
    right: auto;
    left: 0;
    bottom: 0;
    opacity: 1;
    transform: scale(1);
    /* Масштабируем картинку до нормального размера */
}

.page-articles__popular-item.active .page-articles__popular-circle {
    opacity: 0;
    transform: scale(1);
    /* Масштабируем картинку до нормального размера */

}

.page-articles__popular-item.active img {
    opacity: 1;
    transform: scale(1);
    /* Масштабируем картинку до нормального размера */
    transition: opacity 0.6s ease, transform 0.6s ease;
}

