            }

            &_item {
                margin-bottom: 14px;

                & input[type="checkbox"] {
                    width: 22px;
                    height: 22px;

                    &::before {
                        top: 3px;
                        left: 7px;
                        width: 6px;
                        height: 10px;
                        border-width: 0 2px 2px 0;
                    }
                }

                & label {
                    font-size: 16px;
                }
            }

            &_quantity {
                margin-top: 30px;
                font-size: 12px;
            }
        }

        &__card {





            &_btn {
                text-align: center;
            }
        }
    }
}

