/* breadcrumbs */
.breadcrumbs-1 {
    padding: 20px 0 40px 0;
    font-family: var(--font-family);
}

.breadcrumbs-1,
.breadcrumbs-1 li {
    align-items: center;
    display: flex;
    gap: 6px;
    flex-wrap: wrap;
}

.breadcrumbs-1 li {
    font-size: 14px;
    color: var(--color-zelenyy);
}

.breadcrumbs-1 li a {
    font-size: 14px;
    color: var(--color-zelenyy);
    &:hover {
        color: #405f6a;
    }
}

.breadcrumbs-1 li:after {
    content: ">";
    display: block;
    font-size: 20px;
    color: var(--color-temno-zelenyy-1);
    font-size: inherit;
}

.breadcrumbs-1 li:first-child {
    position: relative;
    padding-left: 24px;
}

.breadcrumbs-1 li:first-child::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    width: 16px;
    height: 16px;
    background: url("./../img/breadcrumbs/home-1.svg") no-repeat center center / contain;
}

.breadcrumbs-1 li:last-child {
    color: var(--tekst-svetlyy);
}

.breadcrumbs-1 li:last-child::after {
    content: none;
}

.page-top__title-1 {
    margin: 20px 0 40px 0;
}

@media (max-width: 688px) {
    .breadcrumbs-1 {
        padding: 20px 0;
    }

    .page-top__title-1 {
        margin: 5px 0 40px 0;
    }
}
